import { GetComponentProps, useEditor, useEditorActions } from 'topwrite';
import { ReactComponent as Icon } from '../images/icon.svg';

type Props = GetComponentProps<'editor:tool'>

export default function Tool({ Component }: Props) {

    const editor = useEditor();

    const handler = () => {
        const { isBlockStart, insertText, isBlockEnd } = useEditorActions();

        if (editor.isDocument) {

        } else {
            let template = [
                '```[api]',
                'get:/url',
                '*string:id=默认值#说明文字',
                'name#说明文字',
                '<<<',
                'success',
                '',
                '<<<',
                'error',
                '',
                '```'
            ];

            if (!isBlockStart(editor)) {
                editor.insertText('\n');
            }
            insertText(editor, template.join('\n'));
            if (!isBlockEnd(editor)) {
                editor.insertText('\n');
            }
        }
    };

    return <Component
        title={'API'}
        handler={handler}
    >
        <Icon />
    </Component>;
};


